<template>
    <div class="wrap">
        <div>
            <div>
                <my-input class="search" placeholder="ФИО" v-model="SearchStudent"></my-input>
            </div>

            <div class="rr">
                <student-list :students="SortedStudents" @chooseStudent="chooseStudent($event)"></student-list>
            </div>

        </div>

        <my-dialog v-model:show="showStuds">
            <h1>Проставленные оценки</h1>
            <div class="dialog">ФИО студента: {{ `${this.user.name} ${this.user.surname} ${this.user.secondname}` }}</div>
            <grade-list :grades="grades"></grade-list>
        </my-dialog>
        <div v-if="SortedStudents.length === 0">
            Список студентов пуст
        </div>
        <div v-if="IsError">
            Ошибка: {{errorCode}}
        </div>
    </div>
</template>

<script>
    import UserService from "@/service/UserService";
    import StudentList from "@/components/StudentList";
    import MyInput from "@/components/UI/MyInput";
    import PostsService from "@/service/PostsService";
    import SubscribeList from "@/components/SubscribeList";
    import GradeList from "@/components/GradeList";

    export default {
        components: {GradeList, SubscribeList, MyInput, StudentList},
        data(){
            return{
                students: [],
                showStuds: false,
                user: {},
                chosenUser: {},
                chosenUserId: null,
                chosen: '1',
                SearchStudent: '',
                grades: [],
                errorCode: '',
                loader: false,
            }
        },
        mounted() {

            this.user = this.$store.getters['registration/User'];
            this.getStudents();
        },
        methods: {
            async getStudents(){
                try {
                    const response = await UserService.getAll();
                    this.students = response.data;
                } catch (e) {
                    this.errorCode = e.response
                }
            },

            async change(){

            },
            async chooseStudent(e){
                this.showStuds = true;
                this.chosenUserId = e;
                this.chosenUser = this.students.find(p => p.id === e)

                try {
                    this.loader = true;
                    const response = await PostsService.getGrades(e)
                    this.grades = response.data
                }
                catch (e) {
                    this.errorCode = e
                    console.log(e?.data)
                }
                finally {
                    this.loader = false
                }

                this.user = this.$store.getters['registration/User'];
            },

        },
        computed: {
            SortedStudents() {
                return [...this.students]
                    .filter(s => s.role === "user")
                    .filter(student => `${student.name} ${student.surname} ${student.secondName}`.toLowerCase().includes(this.SearchStudent.toLowerCase()))
            },
            IsError(){
                return this.errorCode !== ''
            },
        }
    }
</script>

<style scoped>
    .dialog{
        margin: 10px;
    }

    .day{
    }

    .wrap{
        margin-top: 30px;
    }
    .search{
        margin-top: 50px;
    }
</style>


